@media ( max-width:954px ) {
    .navb ul{
        /* clear: right; */
        display: flex;
        flex-direction: column;
        float: none;

        justify-content: center;
        align-items: center;
        

        
        background-color: rgb(156, 30, 176);

        width:50vw;
        height: 50vh;
        

        font-size: 25wh;
        font-weight: bold;
        line-height: 2;
        padding-top: 50px;
        padding-bottom: 50px;
        margin: 12px 0 0 -50vw;
    }


    .navb ul li{
        clear: left;
    }

    .bar{
        display: inline;
    }

   
   

}

@media (max-width:1459px)
{
     #About-us{
        flex-direction: column;
       
        align-items: center;
     }

     .contact-us h1{
        text-align: center;
     }
}
@media (max-width:506px){
    
 .intro h1{
        font-size: 8vw;
    }
    .intro h1 span{
        font-size: 8.5vw;
    }

    .intro h2{
        font-size: 5.5vw;
    }
    .intro lable{
        font-size: 4.8vw;
    }

    .intro .icon{
        padding: 0 5vw 0 5vw ;
    }

    .intro .icon a svg{
        margin-left: 5vw;
    }

    .contact-us button{
       margin-left: 0;
        width: 30vw !important;
    }

    .contact-us a{
        margin-left: 0;
         
     }
}
@media  (max-width:954px)
{
    #Home img{
        width: 80vw;
    }
    .hometext{
        padding-top: 4vh;
    }

    #Home h2{
        font-size: 7vw;
    }
    #Home h1{
        font-size: 8vw;
    }
    #Home h3{
        font-size: 7vw;
    }
    #Home a{
        width: 40vw;
        height: 53px;
        font-size: 5.2vw;
        padding-top: 11px;
    }
    .intro .icon{
        margin-top: 0;
        padding-left: 0%;
        font-size: 6.8vw;
    }
   
    .intro{
        width: fit-content;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-left: 0%;
        padding-right: 0%;
        border: 0;
    }
    .contact-us{
        display: flex;
        flex-direction: column;
        align-items: center;
        width: fit-content;
        border: 3px solid #b875c2;
        padding-bottom: 20px;
    }
    .contact-us div{
        width: 60vw;
        margin-top: 20px;
        /* height: 6.5vh; */
        padding: 2px 5px 2px 5px;
    }
   .last{
    margin-left: 0;
   }
    .contact-us .sug{
        /* height: 25vh; */
        padding: 10px;
    }

    .contact-us .sug textarea{
        margin-left: 10px;
        width: 95%;
        height: 85%;
    }

    .contact-us button{
        margin-top: 20px;
        margin-bottom: 20px;
        width: 15vw;
    }


}

@media( max-width:1343px)
{
    .homecont{
        clear: left;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .homeimg{
        clear: left;
    }
    .hometext{
        clear: right;
        text-align: center;
    }
    
}

.disable{
    pointer-events: none;
    opacity: 0.5;
}


.navb ul li{
    float: left;
}


 